  background: $blackOscuro; 
  // background-image: linear-gradient(rgba(20, 10, 10, 0.897), rgba(31, 28, 17, 0.822)),
   background-image:url("../../imagenes/burgerNav1.png");
  // background-image:url("../../imagenes/backgroundBurgerN1.png");
  // background-image:url("../../imagenes/appBurger.gif");
