.container {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  .img {
    width: 12rem;
    // height: 3rem;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
  }
  .container-title {
    // flex-direction: column;
    // margin: 0 3rem;

    // width: 100%;
    // align-items: center;
    // display: flex;
    // flex-direction: column;
    // /* margin: 0 3rem; */
    // width: 100%;
    // align-items: center;
    background: #0000009e;
    display: flex;
    flex-direction: column;
    /* margin: 0 3rem; */
    // width: 40%;
    width: 38rem;
    align-items: center;
    height: 100%;
    justify-content: center;
    img {
      width: 8rem;
    }
  }
}

.button_options {
  // display: flex;
  // flex-direction: column;

  display: flex;
  flex-direction: column;
  width: 20rem;

  // button {
