    button {
    // background-color:$yellowColor;
    // border-radius: 13px;
    // font-size: 3rem;
    // margin: 2rem;
    // padding: 1rem 2rem;
    // -webkit-text-stroke: 1px rgb(54, 44, 15);
    // cursor: pointer;
    // border-style: solid;
    // border-color: #ffc107;
    // /* border-width: 10px; */
    // font-weight: bold;
    // color: #3d2904;
    background-color: #1c1b19;
    border-radius: 13px;
    // font-size: 3rem;
    font-size: 2rem;
    margin: 2rem;
    padding: 1rem 2rem;
    -webkit-text-stroke: 1px #362c0f;
    cursor: pointer;
    border-style: solid;
    border-color: #ffc107;
    /* border-width: 10px; */
    font-weight: bold;
    color: #c89d4e;
    @keyframes button {
      0%, 20%, 60%, 100% {
        -webkit-transform: translateY(0);
        transform: translateY(0);
      }
    
      40% {
        -webkit-transform: translateY(-20px);
        transform: translateY(-20px);
      }
    
      80% {
        -webkit-transform: translateY(-10px);
        transform: translateY(-10px);
      }
    }
    &:hover {
      animation: button 1s;
      background: #362c0f;
      color: #f1c375;
    }
    }


