  }
}

@media(max-width:708px) {
  .container {
    background-image:url("../../imagenes/burgerNav4.png");
  }
}

@media(max-width:574px) {
  .container {
    background-image:url("../../imagenes/burgerNav4.png");
    .container-title {
      img {
        width: 8rem;
      }
      h1 {
        font-size: 3rem;
      }
    }
    h2 {
      color: #f1c375;
      font-family: "Roboto";
      font-size: 4rem;
      -webkit-text-stroke: 1px #362c0f;
      margin: 0rem;
    }
  }


}

